.search-box{width:280px;margin-right:10px;margin-bottom:10px;display:inline-block;vertical-align:middle;border:1px solid #dcdee2;border-radius:4px}.search-box:focus{-webkit-box-shadow:0 0 0 2px rgba(45,140,240,.2);box-shadow:0 0 0 2px rgba(45,140,240,.2)}.search-box:focus,.search-box:hover{border-color:#57a3f3}.search-box .ivu-input{border:0;outline:none;-webkit-box-shadow:none;box-shadow:none}.search-box .ivu-input-group-prepend{border:0;border-right:1px solid #dcdee2}.search-box .ivu-input-group{top:0}.search-box .ivu-select{display:table-cell}.search-box .ivu-select .ivu-select-selection{border:0;outline:0;-webkit-box-shadow:0!important;box-shadow:0!important;border-bottom-left-radius:0!important;border-top-left-radius:0!important}.search-box .ivu-select-visible .ivu-select-selection{-webkit-box-shadow:none!important;box-shadow:none!important}.box-select{display:inline-table;vertical-align:middle}.box-select label{display:table-cell;word-break:keep-all;white-space:nowrap;vertical-align:middle;padding:4px 7px;font-size:inherit;font-weight:400;line-height:1;color:#515a6e;text-align:center;background-color:#f8f8f9;border-right:1px solid #dcdee2;border-bottom-left-radius:4px;border-top-left-radius:4px}.label-box{margin-bottom:10px}.label-box .label-text{display:inline-block;width:140px;text-align:right}.label-box .label-slot{display:inline-block;width:300px}